import React from "react";
import { useRecoilValue } from "recoil";
import { MainNavDataAtom } from "../recoil/atoms/state";


const Nav: React.FC = () => {
  const nav_data = useRecoilValue(MainNavDataAtom);
  return (
    <>
      {nav_data.map((d, i) =>
        <nav key={i} id={d.device}>
          <ul>
            <>
              {d.nav.map((d, i) =>
                <li key={i}><a href="">{d}</a></li>
              )}
            </>
          </ul>
        </nav>
      )}
    </>
  )
}

export default Nav;