import React from 'react';
import Title from './Title';
import { useRecoilValue } from 'recoil';
import { Section5DataAtom, TitleDataAtom } from '../recoil/atoms/state';

const Section5: React.FC = () => {
  const sec5_data = useRecoilValue(Section5DataAtom);
  const title_data = useRecoilValue(TitleDataAtom);
  const title = title_data[4].title;
  const subTitle = title_data[4].subTitle;

  return (
    <section id="section05">
      <div className="inner">
        <Title title={title} subTitle={subTitle} />
      </div>
      <div className="sec05_banner_wrap">
        <div className="sec05_banner">
          <>
            {sec5_data.map((d, i) => {
              return (
                <a key={i} href=''>{d}</a>
              )
            })
            }
          </>
        </div>
      </div>
    </section>
  )
}

export default Section5;