import React from 'react';
import Title from './Title';
import { useRecoilValue } from 'recoil';
import { Section1DataAtom, TitleDataAtom } from '../recoil/atoms/state';

const Section1: React.FC = () => {
  const sec1_data = useRecoilValue(Section1DataAtom);
  const title_data = useRecoilValue(TitleDataAtom)
  const title = title_data[0].title;
  const subTitle = title_data[0].subTitle;

  return (
    <section id='section01'>
      <div className="inner">
        <Title title={title} subTitle={subTitle} />
        <div className="content_dna">
          <ul>
            <>
              {sec1_data.map((d, i) => {
                return (
                  <li key={i} data-aos={d.aos} data-aos-delay={d.aosDelay}>
                    <div className="image"><img src={process.env.PUBLIC_URL + "/assets/images/" + d.src} alt={d.src} /></div>
                    <div className="text">
                      <p className="sub">{d.title}</p>
                      <p className="title">{d.sub}</p>
                      <p className="detail">
                        {d.desc.map((d, i) => (
                          <span key={i}>{d}<br /></span>
                        ))}
                      </p>
                    </div>
                  </li>
                )
              })}
            </>
          </ul>
        </div>
      </div>
    </section>
  )
}

export default Section1;