import React from "react";

interface TitleProps {
  title: string,
  subTitle: string
}
const Title: React.FC<TitleProps> = ({ title, subTitle }) => {
  return (
    <>
      <div className="desc">
        <span></span>
        <p className="title">{title}</p>
        <p className="subtitle">{subTitle}</p>
      </div>
    </>
  )
}

export default Title;