import React from 'react';
import Title from './Title';
import { useRecoilValue } from 'recoil';
import { Section4DataAtom, TitleDataAtom } from '../recoil/atoms/state';

const Section4: React.FC = () => {
  const sec4_data = useRecoilValue(Section4DataAtom);
  const title_data = useRecoilValue(TitleDataAtom);
  const title = title_data[4].title;
  const subTitle = title_data[4].subTitle;

  return (
    <section id="section04">
      <div className="inner">
        <Title title={title} subTitle={subTitle} />
        <div className="content_culture">
          <ul>
            <>
              {sec4_data.map((d, i) => {
                return (
                  <li key={i} data-aos="flip-up" data-aos-delay={d.aosDelay}>
                    <div className="image"><p>{d.initial}</p>
                      <img src={process.env.PUBLIC_URL + '/assets/images/culture_bg.png'} alt="" />
                    </div>
                    <div className="text">
                      <p className="title">{d.title}</p>
                      <p className="detail">{d.desc}</p>
                    </div>
                  </li>
                )
              })}
            </>
          </ul>
        </div>
      </div>
    </section>
  )
}

export default Section4;