import React, { useEffect, useState } from 'react';
import Title from './Title';
import { Swiper, SwiperSlide } from 'swiper/react';
import SwiperCore from 'swiper';
import 'swiper/css';
import { useRecoilValue } from 'recoil';
import { Section2DataAtom, TitleDataAtom } from '../recoil/atoms/state';

const Section2: React.FC = () => {
  const sec2_data = useRecoilValue(Section2DataAtom);
  const title_data = useRecoilValue(TitleDataAtom);
  const title = title_data[1].title;
  const subTitle = title_data[1].subTitle;

  let winw: number = 0;
  const [swiperInstance, setSwiperInstance] = useState<SwiperCore | null>(null);
  const [isSwiperActive, setIsSwiperActive] = useState(false);

  const updateSwiperStatus = () => {
    const width = window.innerWidth;
    if (width < 720) {
      setIsSwiperActive(true);
    } else {
      setIsSwiperActive(false);
      if (swiperInstance) {
        swiperInstance.destroy(false, true);
        setSwiperInstance(null);
      }
    }
  };

  useEffect(() => {
    updateSwiperStatus();
    window.addEventListener('resize', updateSwiperStatus);
  }, [swiperInstance]);

  return (
    <section id="section02">
      <div className="inner">
        <Title title={title} subTitle={subTitle} />
        {isSwiperActive ? (
          <Swiper
            onSwiper={setSwiperInstance}
            slidesPerView={'auto'}
            initialSlide={0}
            observer={true}
            observeParents={true}
            loop={true}
            className="sec02_swiper"
          >
            <>
              {sec2_data.map((d, i) => {
                return (
                  <SwiperSlide key={i}><a href=""><span dangerouslySetInnerHTML={{ __html: d.name }}></span> <p>{d.initial}</p></a></SwiperSlide>
                )
              })}
            </>
          </Swiper>
        ) : (
          <div className="sec02_swiper">
            <div className="swiper-wrapper">
              <>
                {sec2_data.map((d, i) => {
                  return (
                    <div key={i} className="swiper-slide" data-aos="flip-right" data-aos-delay={d.aosDelay}><a href=""> <span dangerouslySetInnerHTML={{ __html: d.name }}></span><p>{d.initial}</p></a></div>
                  )
                })
                }
              </>
            </div>
          </div>
        )}
      </div>
    </section>
  );
};



export default Section2;