import React from 'react';
import Title from './Title';
import { useRecoilValue } from 'recoil';
import { Section3DataAtom, TitleDataAtom } from '../recoil/atoms/state';

const Section3: React.FC = () => {
  const sec3_data = useRecoilValue(Section3DataAtom);
  const title_data = useRecoilValue(TitleDataAtom);
  const title = title_data[4].title;
  const subTitle = title_data[4].subTitle;

  return (
    <section id="section03">
      <div className="inner">
        <Title title={title} subTitle={subTitle} />
        <div className="content_benefits">
          <ul>
            <>
              {sec3_data.map((d, i) => {
                return (
                  <li key={i} data-aos={i % 2 === 0 ? 'fade-right' : 'fade-left'}>
                    <div className="image"><img src={process.env.PUBLIC_URL + '/assets/images/benefit_' + (i + 1) + '.png'} alt="" />
                    </div>
                    <div className="text">
                      <p>{d.desc}</p>
                      <strong>{d.strong}</strong>
                    </div>
                  </li>
                )
              })}
            </>
          </ul>
        </div>
      </div>
    </section>
  )
}

export default Section3;