import React, { useEffect } from "react";
import Main from "./Main";
import Section1 from "./Section1";
import Section2 from "./Section2";
import Section3 from "./Section3";
import Section4 from "./Section4";
import Section5 from "./Section5";
import Section6 from "./Section6";
import Footer from "./Footer";

import AOS from "aos";
import "aos/dist/aos.css";
import gsap from 'gsap';
import ScrollToPlugin from "gsap/ScrollToPlugin";

const Home: React.FC = () => {
  useEffect(() => {
    gsap.registerPlugin(ScrollToPlugin);
    AOS.init({
      easing: "ease-in-out-sine",
      once: true
    });

    let video = document.getElementById("main_video") as HTMLVideoElement;
    video.addEventListener("loadeddata", function () {
      video.play();
    });
    video.addEventListener("ended", function () {
      video.play();
    });

    let mobileTab = document.getElementById("tab") as HTMLAnchorElement;
    let mobileMenu = document.getElementById("mobile") as HTMLElement;
    let body = document.querySelector("body") as HTMLBodyElement;
    let dim = document.querySelector(".dim") as HTMLDivElement;

    if (mobileTab && mobileMenu && body && dim) {
      mobileTab.addEventListener("click", function (e: Event) {
        e.preventDefault();

        if (!mobileTab.classList.contains("on")) {
          mobileTab.classList.add("on");
          gsap.fromTo(mobileMenu, { display: "block", width: 0 }, { width: "50%", duration: 0.1 });
          body.classList.add("stop-scrolling");
          dim.classList.add("active");
        } else {
          mobileTab.classList.remove("on");
          gsap.to(mobileMenu, { width: 0, duration: 0.1 });
          body.classList.remove("stop-scrolling");
          dim.classList.remove("active");
        }
      });
    }
    let n: number = 0;
    let t: number = 0;
    let topPos: number = 0;

    let topBtn = document.querySelector(".top_btn") as HTMLDivElement;

    let sectionList: HTMLElement[] = [];
    sectionList[0] = document.getElementById("main") as HTMLElement;
    let section = document.querySelectorAll("section");

    for (let i = 0; i < section.length; i++) {
      sectionList.push(section[i]);
    }

    let gnb = document.getElementById("desktop") as HTMLDivElement;
    let gnbList = gnb.firstElementChild?.children;

    let mobileGnb = document.getElementById("mobile") as HTMLDivElement;
    let mobileGnbList = mobileGnb.firstElementChild?.children;

    function scrollTrigger(): void {
      t = window.scrollY;
      if (t < sectionList[1].offsetTop) {
        n = 0;
      }
      else if (t < sectionList[2].offsetTop) {
        n = 1;
      }
      else if (t < sectionList[3].offsetTop) {
        n = 2;
      }
      else if (t < sectionList[4].offsetTop) {
        n = 3;
      }
      else if (t < sectionList[5].offsetTop) {
        n = 4;
      }
      else if (t < sectionList[6].offsetTop) {
        n = 5;
        if (window.innerHeight + t === document.body.scrollHeight) {
          n = 6;
        }
      }
      else {
        n = 6;
      }

      if (gnbList && mobileGnbList) {
        for (let i = 0; i < gnbList.length; i++) {
          if (i === (n - 1)) {
            gnbList[i].classList.add("on");
            mobileGnbList[i].classList.add("on");
          }
          else {
            gnbList[i].classList.remove("on");
            mobileGnbList[i].classList.remove("on");
          }
        }
      }

      if (t >= 200) {
        topBtn.style.display = "block";
      }
      else {
        topBtn.style.display = "none";
      }
    }
    function resizeTrigger(): void {
      let winw: number = window.innerWidth;
      if (winw >= 720) {
        if (body && body.classList.contains('stop-scrolling') && mobileTab && mobileMenu && dim) {
          mobileTab.classList.remove("on");
          gsap.to(mobileMenu, { width: 0, display: "none", duration: 0.1 });
          body.classList.remove("stop-scrolling");
          dim.classList.remove("active");
        }
      }
    }


    scrollTrigger();
    resizeTrigger();

    window.addEventListener("scroll", scrollTrigger);
    window.addEventListener("resize", resizeTrigger);

    topBtn.addEventListener("click", function (e: Event) {
      e.preventDefault();
      gsap.to(window, { scrollTo: 0, duration: 0.3 });
    });

    if (gnbList && mobileGnbList) {
      for (let i = 0; i < gnbList.length; i++) {
        gnbList[i].addEventListener("click", function (e) {
          e.preventDefault();

          n = i + 1;
          topPos = sectionList[n].offsetTop;

          gsap.to(window, { scrollTo: topPos, duration: 0.4 });
        });

        mobileGnbList[i].addEventListener("click", function (e) {
          e.preventDefault();

          n = i + 1;
          topPos = sectionList[n].offsetTop;

          mobileTab.classList.remove("on");
          gsap.to(mobileMenu, { width: 0, duration: 0.1 });
          body.classList.remove("stop-scrolling");
          dim.classList.remove("active");

          gsap.to(window, { scrollTo: topPos, delay: 0.4, duration: 0.4 });
        });
      }
    }
    let privacy = document.querySelector("#footer .inner > ul > li:first-child");
    let privacy2Depth = privacy?.children[1] as HTMLElement;

    if (privacy) {
      privacy.addEventListener("click", function (e) {
        e.preventDefault();

        if (privacy2Depth && privacy2Depth.style.display === "block") {
          gsap.to(privacy2Depth, {
            opacity: 0, duration: 0.3, onComplete: function () {
              privacy2Depth.removeAttribute("style");
            }
          });
        }
        else if (privacy2Depth) {
          gsap.fromTo(privacy2Depth, { display: "block", opacity: 0 }, { opacity: 1, duration: 0.2 });
        }
      });
    }
  }, []);

  return (
    <div className="wrapper">
      <Main />
      <Section1 />
      <Section2 />
      <Section3 />
      <Section4 />
      <Section5 />
      <Section6 />
      <Footer />
    </div>
  )
}

export default Home;