import React from 'react';
import Title from './Title';
import { useRecoilValue } from 'recoil';
import { TitleDataAtom } from '../recoil/atoms/state';

const Section6: React.FC = () => {
  const title_data = useRecoilValue(TitleDataAtom);
  const title = title_data[5].title;
  const subTitle = title_data[5].subTitle;

  return (
    <section id="section06">
      <div className="inner">
        <Title title={title} subTitle={subTitle} />
        <div className="form">
          <div className="top_form">
            <div className="input_wrap">
              <input type="text" id="name" placeholder="NAME*" />
              <input type="text" id="e-mail" placeholder="E-mail*" />
              <input type="text" id="job_objective" placeholder="Job Objective*" />
            </div>
            <div className="text_wrap">
              <textarea placeholder="Find Job*"></textarea>
            </div>
          </div>
          <div className="submit">
            <input type="submit" value=" " />
          </div>
        </div>
      </div>
    </section>
  )
}

export default Section6;