import React from 'react';
import Nav from './Nav';

const Main: React.FC = () => {
  return (
    <div className="main">
      <div id="main">
        <header>
          <div className="inner">
            <h1><a href="">logo</a></h1>
            <Nav />
            <a href="" id="tab"><span></span></a>
            <div className="dim"></div>
          </div>
        </header>
        <div className="keytext">
          <p className="title" data-aos="fade-up" >We believe in music</p>
          <p className="subtitle" data-aos="fade-up" data-aos-delay="300">        .</p>
        </div>
        <div className="bg"><video src={`${process.env.PUBLIC_URL}/assets/images/bg_video.mp4`} id="main_video" autoPlay muted playsInline poster=""></video></div>
      </div>
    </div>
  )
}

export default Main;